import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import "../styles/register.css";
import axios from "axios";
import toast from "react-hot-toast";
import { FiEye, FiEyeOff } from "react-icons/fi";

axios.defaults.baseURL = process.env.REACT_APP_SERVER_DOMAIN;

function Register() {
  const [file, setFile] = useState("");
  const [loading, setLoading] = useState(false);
  const [formDetails, setFormDetails] = useState({
    firstname: "",
    lastname: "",
    email: "",
    password: "",
    confpassword: "",
  });
  const [showPassword, setShowPassword] = useState(false);
  const [showConfPassword, setShowConfPassword] = useState(false);

  const navigate = useNavigate();

  const inputChange = (e) => {
    const { name, value } = e.target;
    setFormDetails({ ...formDetails, [name]: value });
  };

  const onUpload = async (element) => {
    setLoading(true);
    if (element.type === "image/jpeg" || element.type === "image/png") {
      const data = new FormData();
      data.append("file", element);
      data.append("upload_preset", process.env.REACT_APP_CLOUDINARY_PRESET);
      data.append("cloud_name", process.env.REACT_APP_CLOUDINARY_CLOUD_NAME);
      fetch(process.env.REACT_APP_CLOUDINARY_BASE_URL, {
        method: "POST",
        body: data,
      })
        .then((res) => res.json())
        .then((data) => setFile(data.url.toString()))
        .finally(() => setLoading(false));
    } else {
      setLoading(false);
      toast.error("Please select an image in jpeg or png format");
    }
  };

  const formSubmit = async (e) => {
    e.preventDefault();
    if (loading) return;
    if (!file) return toast.error("Please upload a profile picture");

    const { firstname, lastname, email, password, confpassword } = formDetails;

    if (!firstname || !lastname || !email || !password || !confpassword) {
      return toast.error("All fields are required");
    }
    if (firstname.length < 3) return toast.error("First name must be at least 3 characters");
    if (lastname.length < 3) return toast.error("Last name must be at least 3 characters");
    if (password.length < 5) return toast.error("Password must be at least 5 characters");
    if (password !== confpassword) return toast.error("Passwords do not match");

    try {
      await toast.promise(
        axios.post("/user/register", { firstname, lastname, email, password, pic: file }),
        {
          pending: "Registering user...",
          success: "User registered successfully",
          error: "Unable to register user",
        }
      );
      navigate("/login");
    } catch (error) {
      toast.error("Registration failed");
    }
  };

  return (
    <section className="register-section flex-center">
      <div className="register-container flex-center">
      

        <h2 className="form-heading">Sign Up</h2>

        <form onSubmit={formSubmit} className="register-form">
          {/* First name + Last name */}
          <div className="form-row">
            <input
              type="text"
              name="firstname"
              className="form-input"
              placeholder="First name"
              value={formDetails.firstname}
              onChange={inputChange}
            />
            <input
              type="text"
              name="lastname"
              className="form-input"
              placeholder="Last name"
              value={formDetails.lastname}
              onChange={inputChange}
            />
          </div>

          <input
            type="email"
            name="email"
            className="form-input"
            placeholder="Email"
            value={formDetails.email}
            onChange={inputChange}
          />

          <input
            type="file"
            onChange={(e) => onUpload(e.target.files[0])}
            name="profile-pic"
            id="profile-pic"
            className="form-input"
          />

          {/* Password + Confirm password */}
        <div className="form-row">
  <div className="password-wrapper">
    <input
      type={showPassword ? "text" : "password"}
      name="password"
      className="form-input password-input"
      placeholder="Password"
      value={formDetails.password}
      onChange={inputChange}
    />
    <span
      className="password-toggle"
      onClick={() => setShowPassword(!showPassword)}
    >
      {showPassword ? <FiEyeOff /> : <FiEye />}
    </span>
  </div>

  <div className="password-wrapper">
    <input
      type={showConfPassword ? "text" : "password"}
      name="confpassword"
      className="form-input password-input"
      placeholder="Confirm Password"
      value={formDetails.confpassword}
      onChange={inputChange}
    />
    <span
      className="password-toggle"
      onClick={() => setShowConfPassword(!showConfPassword)}
    >
      {showConfPassword ? <FiEyeOff /> : <FiEye />}
    </span>
  </div>
</div>

          <button type="submit" className="btn form-btn" disabled={loading}>
            Sign Up
          </button>
        </form>

      <p>
  Already a user?
  <NavLink className="login-link" to="/login">
    Log in
  </NavLink>
</p>

          {/* Back to Home button */}
       <button
  type="button"
  className="btn back-btn"
  onClick={() => navigate("/")}
>
  &larr; Back
</button>
      </div>
    </section>
  );
}

export default Register;
